export const handleMenu = () => {
  const menu = document.querySelector('.menu');
  const nav = document.querySelector('.nav');
  const navPanel = document.querySelector('.header__container');

  navPanel.addEventListener('click', (e) => {
    const target = e.target;
    const isClassInMenu = menu.classList.contains('active');
    const isClassInNav = nav.classList.contains('active');
    const isNavItem = target?.classList.contains('nav__item');
    const isMenu = target?.classList.contains('menu');

    if (isMenu) {
      !isClassInMenu ? menu?.classList.add('active') : menu?.classList.remove('active');
      !isClassInNav ? nav?.classList.add('active') : nav?.classList.remove('active');
    }

    if (isNavItem) {
      menu?.classList.remove('active');
      nav?.classList.remove('active');
    }
  });
};
